import InfoOutlined from "@mui/icons-material/InfoOutlined";
import SecurityOutlined from "@mui/icons-material/SecurityOutlined";
import { Card } from "@mui/material";
import sortBy from "lodash/sortBy";
import { Outlet, useLocation, useNavigate, useParams } from "react-router";
import { useTask } from "../../../../Application";
import { useAuth } from "../../../../auth";
import ResponsiveContainer from "../../../../components/ResponsiveContainer";
import ResponsiveTabBar from "../../../../components/ResponsiveTabBar";
import { TabView } from "../../../../tabs";


const Settings = () => {

    const location = useLocation();
    const navigate = useNavigate();
    const params = useParams();
    // const navigateTask = useNavigateTask();

    const { inject } = useAuth();

    const task = useTask('Authentication.settings');

    const injectedHooks = Object.assign({}, ...(inject('Authentication', 'Settings.Hooks') || []));


    return (
        <TabView {...task}>
            <ResponsiveContainer fullHeight>
                <Card
                    variant="outlined"
                    sx={{
                        flexGrow: 1,
                        display: 'flex',
                        flexDirection: 'column'
                    }}
                >
                    <ResponsiveTabBar
                        tabs={sortBy([
                            {
                                label: 'اطلاعات حساب',
                                value: `/account/settings`,
                                icon: <InfoOutlined />,
                                priority: 100
                            },
                            {
                                label: 'رمز عبور',
                                value: `/account/settings/security`,
                                icon: <SecurityOutlined />,
                                priority: 100
                            },
                            ...(inject('Authentication', 'Settings.TabsList', { ...injectedHooks }) || [])
                        ].map(o => ({
                            ...o, onClick: () => navigate(
                                o.value,
                                { state: { prev: location } }
                            )
                        })), o => o.priority)}
                        value={location.pathname}
                    >
                        <Outlet />
                    </ResponsiveTabBar>
                </Card>

                {/* {inject('People', 'PersonDetails.Footer', { person, loading, reload, params, ...injectedHooks })} */}
            </ResponsiveContainer>
        </TabView>
    )
}

export default Settings;